<template>
	<v-container fluid class="principal">
		<v-layout row>
		    <v-flex xs12 text-xs-center>
		      <v-card class="card">
		        <v-toolbar color="cyan" dark class="navbar-fixed-propia">
		          <v-icon v-on:click="go('/history')" class="goback" color="white"  left large light>keyboard_arrow_left</v-icon>
		          <v-toolbar-title class="title">History | LGBTQ Community</v-toolbar-title>
		          <v-spacer></v-spacer>
		          <v-btn icon v-on:click="teamDialog.active = !teamDialog.active">
		          	<v-icon>info</v-icon>
		          </v-btn>
		        </v-toolbar>
		        <br>
	        	<h1 class="flat">Diversidad Sexual</h1>
		      </v-card>
		    </v-flex>
		</v-layout>
		<br>
		<v-layout row :style="{paddingTop: '40px'}">
			<v-flex xs12 class="contenido">
			<center>
				<visorimages :images="images"></visorimages>
			</center>
			</v-flex>
	        <v-dialog class="teamDialog" v-model="teamDialog.active" persistent max-width="400">
		      <v-card>
		        <v-card-title class="headline">Work Team</v-card-title>
		        <v-card-text>
		        	<v-list>
		        		 <template v-for="(item, index) in teamDialog.people" class="item">
				            <v-list-tile avatar :key="item.name" @click="">
				              <v-list-tile-content>
				                <v-list-tile-title v-html="item.name"></v-list-tile-title>
				              </v-list-tile-content>
				            </v-list-tile>
				          </template>
		        	</v-list>
		        </v-card-text>
		        <v-card-actions>
		          <v-spacer></v-spacer>
		          <center>
		          	<v-btn color="green darken-1" flat @click.native="teamDialog.active = false">Cerrar</v-btn>
		          </center>
		        </v-card-actions>
		      </v-card>
		    </v-dialog>
	    </v-layout>
	    <v-container  fluid style="min-height: 0;width: 98% !important; margin-left: 1% !important;" grid-list-lg>
	        <v-layout row wrap>
	          <v-flex xs12 md4 v-for="(card,index) in acronyms" :key="index">
	            <v-card :color="card.color" :class="[card.colortext,'card-acronyms-all']">
	              <v-card-title primary-title>
	                <div class="headline card-acronyms"><center><h2 v-html="card.acronym"></h2></center></div>
	                <div>{{ card.text }}</div>
	              </v-card-title>
	            </v-card>
	          </v-flex>
	      </v-layout>
	      <br>
	      <div class="steppers-area-propia">
	      	<v-stepper v-model="e13" vertical>
			    <v-stepper-step step="1" class="stepindicator-press" v-on:click.native="e13=1">
			      Edad Antigua
			    </v-stepper-step>
			    <v-stepper-content step="1">
			      <v-card color="white" class="mb-5" style="min-height: 200px !important;">
			      	<p><strong>3000 a.c </strong>:
			      		Desde el periodo sumerio existian las relaciones entre individuos del mismo sexo, en esta epoca habían sacerdotes-cantores llamados <code>assinu</code> que significaba "hombre útero" lo cual hace referencia a que estas personas eran homosexuales y lo compartían libremente.</p>
			      	<p>
			      		<strong>2100-560 a.c </strong>: En Babilonia también se documentaba la existencia de la homosexualidad, esta, era considerada algo correcto y no se le daba mayor importancia...Por lo cual no había una condena destinada a individuos con esta orientación sexual, y esto nos trae de nuevo a lo mismo, muchos sacerdotes en esta cultura eran homosexuales y participaban en determinados ritos travestidos.
			      	</p>
			      	<p>
			      		<strong>1800-1077 a.c </strong>: Lo asirios en cambio, eran intolerantes con la homosexualidad masculina, durante un largo tiempo, se condenaba a los homosexuales con la castración, según lo encontrado en una tablilla con la siguiente ley «Si un hombre sodomiza a su compañero y ellos [los jueces] prueban los cargos y le encuentran culpable, le sodomizarán a él y le convertirán en <code>eunuco(Un eunuco es un hombre castrado)</code>».
			      		Sin embargo en el <code>Código de Hammurabi(El código de hammurabi es unos de los conjuntos de leyes más antiguos que se han encontrado)</code>(alrededor del 1770 a. C.), se menciona a las <code>salzikrum (‘hijas-varón’)</code> que podían tomar una o varias esposas y tenían derechos de herencia y de propiedad semejantes a los hombres.
			      	</p>
			      </v-card>
			      <v-btn color="primary" @click.native="e13 = 2">Continuar</v-btn>
			      <v-btn flat @click.native="e13 = 0">Cerrar</v-btn>
			    </v-stepper-content>
			    <v-stepper-step step="2" class="stepindicator-press" v-on:click.native="e13=2">
			    	Edad Media
			    </v-stepper-step>
			    <v-stepper-content step="2">
			      <v-card color="white" class="mb-5" style="min-height: 200px !important;">
			      	<p>
			      		La iglesia condenó la homosexualidad en la baja edad media, pero no se preocupó tanto de este tema hasta el siglo <code><b>XIII</b></code>, en este punto, la iglesía se volvió completamente intolerante al respecto y condenó cada acto homosexual.
			      		Muchos historiadores creen que la tolerancia desapareció por el temor a los grupos con menor numero de individuos los cuales se separaban de la norma mayoritaria. Es bien sabido que los individuos homosexuales son minoría entre los heterosexuales, y siempre se le teme a lo desconocido, probablemente allí fue donde realmente predominó la homofobia... (Miedo exagerado a personas con una orientación sexual menos usual (homosexualidad)) mientras que en la actualidad la discriminación a mi parecer no pasa tanto por el miedo, si no por la falta de respeto y de un valor importantisimo en la sociedad, la <code>Empatia</code> 
			      	</p>
			      </v-card>
			      <v-btn color="primary" @click.native="e13 = 1">Continuar</v-btn>
			      <v-btn flat @click.native="e13 = 0">Cerrar</v-btn>
			    </v-stepper-content>
			    <!--<v-stepper-step step="3" v-on:click.native="e13=3" class="stepindicator-press">
			      Ad templates
			      <small>Alert message</small>
			    </v-stepper-step>
			    <v-stepper-content step="3">
			      <v-card color="grey lighten-1" class="mb-5" style="min-height: 200px !important;"></v-card>
			      <v-btn color="primary" @click.native="e13 = 4">Continuar</v-btn>
			      <v-btn flat @click.native="e13 = 0">Cerrar</v-btn>
			    </v-stepper-content>
			    <v-stepper-step step="4" v-on:click.native="e13=4" class="stepindicator-press">View setup instructions</v-stepper-step>
			    <v-stepper-content step="4">
			      <v-card color="grey lighten-1" class="mb-5" style="min-height: 200px !important;"></v-card>
			      <v-btn color="primary" @click.native="e13 = 1">Continuar</v-btn>
			      <v-btn flat>Cerrar</v-btn>
			    </v-stepper-content>-->
			  </v-stepper>
	      </div>
	  </v-container>
	</v-container>
</template>
<script>
	import visorimages from './../reutilizable/Visorimagenes.vue';
	export default{
		components:{visorimages},
		data(){
			return{
				e13:0,
				teamDialog:{
					active:false,
					people:[
						{name:'Lautaro Umpierrez'},
						{name:'Ashelen Pereira'}
					]
				},
				images:[
					{src:'http://chicaplus.com/wp-content/uploads/2016/10/homosexualidad_opt.jpg',alt:'Homosexual colective',preview:{
							width: '200px',
							height: '125px'
						}},
					{src:'http://ideasqueayudan.com/wp-content/uploads/2016/01/18-ene-2016ParejaGay.jpg',alt:'',preview:{
							width: '200px',
							height: '125px'
						}},
					{src:'http://www.conceive.ca/wp-content/uploads/2013/04/two-beautiful-girls-with-a-baby-on-the-beach.jpg',alt:'',preview:{
							width: '200px',
							height: '125px'
						}
					},

					{src:'https://res.cloudinary.com/teepublic/image/private/s--5XD_JXlA--/t_Preview/b_rgb:262c3a,c_limit,f_jpg,h_630,q_90,w_630/v1474116888/production/designs/686554_1.jpg',alt:'', preview:{
						width: '125px',
						height: '125px'
					}}
					
				],
				acronyms:[
					{acronym:'L', text:'La primera sigla significa `LESBIANS` (Mujeres homosexuales)', color:'red',colortext:'white--text'},
					{acronym:'G', text:'Esta sigla signifa `GAY` (Hombres homosexuales) ',
					color:'orange',colortext:'white--text'},
					{acronym: 'B',text:'La sigla B hace referencia a `BISEXUAL` (Atracción por el genero Masculino y Femenino)',
					color:'yellow',colortext:'black--text'},
					{acronym:'T', text:'Esta sigla significa `TRANSGENDER` (Personas a las cuales su identidad de género no concuerda con su sexo biológico)',color:'green',colortext:'white--text'},
					{acronym:'Q', text:'Esta sigla significa `QUEER` (Personas que están averiguando cual es su orientación sexual o género.)',color:'light-blue',colortext:'white--text'}
				]
			}
		},
		methods:{
			go(url)
			{
				this.$router.push(url);
			},
		},
		computed:{

		}
	}
</script>
<style lang="scss">
	.stepindicator-press
	{
		transition: all 0.3s !important;
		cursor: pointer !important;
		&:hover
		{
			transition: all 0.3s !important;
			background-color: rgba(51,51,51,0.1) !important;
			.stepper__label
			{
				transition: all 0.3s !important;
				color: #333333 !important;
			}
		}
	}
	.navbar-fixed-propia
	{
		position: fixed !important;
		top: 0 !important;
		z-index: 14 !important;
	}

	.container.fluid.principal
	{
		background-color: #fff !important;
		padding: 0 !important;
		margin: 0 !important;
	}
	h1.flat,h2.flat,h3.flat,h4.flat,h5.flat,h6.flat
	{
		font-weight: 300 !important;
	}
	.contenido
	{
		padding: 18px 20px 20px 20px !important;
		background-color: #fff !important;
	}
	.media 
	{
		padding: 0 !important;
		.card__media__background{
			width: 50% !important;

			margin-left: 50% !important;
		}
	}
	@media screen and (max-width: 768px)
	{
		.media 
		{
			.card__media__background{
				width: 100% !important;
				margin-left: 0% !important;
			}

		}
		.card-acronyms-all
		{
			height: 250px !important;
		}
	}
	.padding-left-5
	{
		padding-left: 5px !important;
	}
	.resalted
	{
		font-style: italic !important;
	}
	.teamDialog .dialog
	{
		background-color: #fff !important;
	}
	.teamDialog .item
	{
		border-bottom: 1px solid rgba(51,51,51,0.1) !important;
	}

	.card-acronyms-all
	{
		height: 150px !important;
	}
	.headline.card-acronyms
	{
		width: 100% !important;
	}
	body
	{
		font-family: 'Raleway',sans-serif;
	}
</style>
	


