<template>
	<div>
		<div class="imagePreview" v-for="(image,index) in images" v-on:click="openVisor(image.src,image.alt)" :key='index'> 
			<img :width="image.preview.width" :height="image.preview.height" :src="image.src" :alt="image.alt">
			<v-icon>zoom_in</v-icon>
			<div class="pantallaHover"></div>
		</div>
		<div class="visorImage" v-bind:class="[visor.active ? 'open animated fadeIn' : '']">
			<div class="contenedor-image">
				<center>
					<img :src="visor.image" class="animated rollIn" :alt="visor.alt">
				</center>
			</div>
			<v-icon v-on:click="visor.active=false">close</v-icon>
		</div>
	</div>
</template>

<script>
	export default{
		props:['images'],
		data()
		{
			return{
				
				visor:{
					active:false,
					image:'',
					alt:''
				}
			}
		},
		methods:{
			openVisor(image,alt)
			{
				this.visor.image=image;
				this.visor.alt=alt;
				this.visor.active=true;
			}

		}
	}
</script>
<style lang="scss" scoped>
	.imagePreview
	{
		width: 200px !important;
		height: 125px !important;
		margin-left: 10px !important;
		display: inline-block;
		cursor: pointer !important;
		padding: 0 !important;

		.pantallaHover
		{
			transition: all 0.3s !important;
			height: 127px !important;
			width: 200px !important;
			position: absolute !important;
			margin-top: -132px !important;
		}
		i
		{
			position: absolute !important;
			font-size: 60px !important;
			margin-left: 70px !important;
			margin-top: -95px !important;
			color: #fff !important;
			font-weight: 700 !important;
			z-index: 10 !important;
			display: none;
		}
		&:hover
		{
			.pantallaHover
			{
				transition: all 0.3s !important;
				background-color: rgba(51,51,51,0.6) !important;
			}
			i
			{
				display: block !important;
			}
		}
	}
	.visorImage
	{
		display: none;
		position: fixed !important;
		width: 100% !important;
		height: 100% !important;
		top: 0 !important;
		left: 0 !important;
		background-color: #fff;
		z-index: 1000 !important;
		i
		{
			position: fixed !important;
			top: 20px !important;
			right: 30px !important;
			color: #333333 !important;
			font-weight: 300 !important;
			cursor: pointer !important,
		}
		.contenedor-image
		{
			width: 100%;
			height: 50% !important;
			position: fixed !important;
			top: 15% !important;
			left: 0 !important;
			img
			{
				max-height: 500px !important;
			}
		}
	}
	.visorImage.open
	{
		display: block !important;
	}
	@media screen and (max-width: 768px )
	{
		.visorImage
		{
			.contenedor-image
			{
				img
				{
					width: 98% !important;
					margin-left: 0.5% !important;
				}
			}
		}
	}

</style>


